import axios from "axios";
import { useEffect, useRef, useState } from "react";
import { Link } from "react-router-dom";
import "./style.css";
import { toast, ToastContainer } from "react-toastify";

const RegisteredServicesLayer = () => {
  const [categories, setCategories] = useState([]);
  const [modalOpen, setModalOpen] = useState(false);
  const [modalType, setModalType] = useState("category"); // category | service
  const [modalMode, setModalMode] = useState("add"); // add | edit
  const [modalData, setModalData] = useState({
    name: "",
    id: "",
    categoryId: "",
  });
  const [show, setShow] = useState(false);
  const [file, setFile] = useState(null);
  const [loading, setLoading] = useState(false);
  const [message, setMessage] = useState("");

  const [deleteConfirm, setDeleteConfirm] = useState({ open: false, id: null });

  useEffect(() => {
    fetchCategories();
  }, []);

  const fetchCategories = async () => {
    try {
      const res = await axios.get(
        `${process.env.REACT_APP_BACKEND_URL}/api/category/getAllCategories`
      );
      setCategories(res.data);
    } catch (err) {
      console.error("Error fetching categories:", err);
    }
  };

  const handleModalSubmit = async () => {
    const adminToken = localStorage.getItem("adminToken");
    const { name, id, categoryId } = modalData;

    if (!name.trim()) return alert("Name cannot be empty.");

    try {
      if (modalType === "category") {
        if (modalMode === "edit") {
          await axios.put(
            `${process.env.REACT_APP_BACKEND_URL}/api/category/update/${id}`,
            {
              category_name: name,
            }
          );
          toast.success("Category updated successfully");
        } else {
          await axios.post(
            `${process.env.REACT_APP_BACKEND_URL}/api/category/createCategory`,
            { category_name: name },
            {
              headers: { Authorization: `Bearer ${adminToken}` },
            }
          );
          toast.success("Category created successfully");
        }
      } else if (modalType === "service") {
        if (!categoryId) return alert("Please select a category.");
        await axios.post(
          `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/createService`,
          {
            serviceCategory: categoryId,
            serviceName: name,
          },
          {
            headers: { Authorization: `Bearer ${adminToken}` },
          }
        );
        toast.success("Service created successfully");
      }

      setModalOpen(false);
      setModalData({ name: "", id: "", categoryId: "" });
      fetchCategories();
    } catch (err) {

      // Extract backend error message if available
      const backendMessage =
        err.response?.status === 409
          ? err.response?.data?.error
          : err.response?.data?.message;

      if (backendMessage) {
        toast.error(backendMessage);
      } else {
        if (modalType === "category") {
          if (modalMode === "edit") {
            toast.error("Failed to update category");
          } else {
            toast.error("Failed to create category");
          }
        } else if (modalType === "service") {
          toast.error("Failed to create service");
        } else {
          toast.error("Something went wrong");
        }
      }
    }
  };

  const handleEdit = (cat) => {
    setModalType("category");
    setModalMode("edit");
    setModalData({ id: cat._id, name: cat.category_name });
    setModalOpen(true);
  };

  const handleDelete = async () => {
    try {
      await axios.delete(
        `${process.env.REACT_APP_BACKEND_URL}/api/category/delete/${deleteConfirm.id}`
      );
      fetchCategories();
      toast.success("Category deleted Successfully");
    } catch (err) {
      toast.error("Failed to delete the category");
    } finally {
      setDeleteConfirm({ open: false, id: null });
    }
  };

  const handleFileChange = (e) => {
    setFile(e.target.files[0]);
  };

  const handleUpload = async () => {
    if (!file) {
      alert("‚ö†Ô∏è Please select a file first.");
      return;
    }
    try {
      setLoading(true);
      const formData = new FormData();
      formData.append("file", file);

      const res = await axios.post(
        `${process.env.REACT_APP_BACKEND_URL}/api/categoryService/bulk-upload`,
        formData,
        {
          headers: { "Content-Type": "multipart/form-data" },
        }
      );

      // Save both message & skipped rows
      setMessage({
        text: `‚úÖ ${res.data.message}`,
        createdCount: res.data.createdCount,
        skippedCount: res.data.skippedCount,
        skippedRows: res.data.skippedRows || [],
      });
    } catch (err) {
      setMessage({
        text: `‚ùå Upload failed: ${err.response?.data?.message || err.message}`,
      });
    } finally {
      setLoading(false);
    }
  };

   const modalRef = useRef(null);
    
        useEffect(() => {
          // Handle click outside
          const handleClickOutside = (event) => {
            if (
              modalRef.current &&
              !modalRef.current.contains(event.target) &&
            modalOpen
            ) {
              modalOpen(false);
            }
          };
      
          document.addEventListener("mousedown", handleClickOutside);
      
          // üîë Scroll lock logic
          if (modalOpen) {
            document.body.style.overflow = "hidden"; // Disable background scroll
          } else {
            document.body.style.overflow = "auto"; // Enable background scroll
          }
      
          return () => {
            document.removeEventListener("mousedown", handleClickOutside);
            document.body.style.overflow = "auto"; // Cleanup when component unmounts
          };
        }, [modalOpen]);

         useEffect(() => {
          // Handle click outside
          const handleClickOutside = (event) => {
            if (
              modalRef.current &&
              !modalRef.current.contains(event.target) &&
            show
            ) {
              show(false);
            }
          };
      
          document.addEventListener("mousedown", handleClickOutside);
      
          // üîë Scroll lock logic
          if (show) {
            document.body.style.overflow = "hidden"; // Disable background scroll
          } else {
            document.body.style.overflow = "auto"; // Enable background scroll
          }
      
          return () => {
            document.removeEventListener("mousedown", handleClickOutside);
            document.body.style.overflow = "auto"; // Cleanup when component unmounts
          };
        }, [show]);

  return (
    <div className="dashboard-body__content">
      <div className="card common-card border border-gray-five">
        <div className="card-body">
          <div style={{ display: "flex", justifyContent: "space-between" }}
          className="category-header">
            <div>
              <h4>Available Categories</h4>
            </div>
            <div
              style={{
                display: "flex",
                gap: "5px",
              }}
              className="bulk-addmore"
            >
              <button className="btn btn-success" onClick={() => setShow(true)}>
                Bulk Upload
              </button>
              {/* <button
                onClick={() => {
                  setModalType("category");
                  setModalMode("add");
                  setModalData({ name: "", id: "", categoryId: "" });
                  setModalOpen(true);
                }}
                className="btn btn-success"
              >
                + Add More
              </button> */}
            </div>
          </div>

          {categories.length > 0 ? (
            <div className="table-responsive">
              <table className="table text-body">
                <thead>
                  <tr>
                    <th>SN.</th>
                    <th>Category</th>
                    <th>Action</th>
                  </tr>
                </thead>
                <tbody>
                  {categories.map((cat, index) => (
                    <tr key={cat._id}>
                      <td>{index + 1}</td>
                      <td>
                        <Link
                          to={`/category-services/${cat._id}`}
                          style={{ textDecoration: "underline" }}
                        >
                          {cat.category_name}
                        </Link>
                      </td>
                      <td>
                        <button
                          className="btn btn-sm btn-primary me-2"
                          onClick={() => handleEdit(cat)}
                        >
                          Edit
                        </button>
                        {/* <button
                          className="btn btn-sm btn-danger"
                          onClick={() =>
                            setDeleteConfirm({ open: true, id: cat._id })
                          }
                        >
                          Delete
                        </button> */}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <p className="text-muted">No categories available.</p>
          )}
        </div>
      </div>

      {/* Shared Add/Edit Modal */}
      {modalOpen && (
        <div className="modal-overlay" style={{padding:"20px"}}>
          <div className="modal-content" style={{padding:"20px"}}>
            <h3>
              {modalMode === "edit" ? "Edit" : "Add New"}{" "}
              {modalType === "category" ? "Category" : "Service"}
            </h3>

            {modalType === "service" && (
              <div className="form-group">
                <label>Select Category</label>
                <select
                  value={modalData.categoryId}
                  onChange={(e) =>
                    setModalData({ ...modalData, categoryId: e.target.value })
                  }
                  className="form-control"
                >
                  <option value="">Select Category</option>
                  {categories.map((cat) => (
                    <option key={cat._id} value={cat._id}>
                      {cat.category_name}
                    </option>
                  ))}
                </select>
              </div>
            )}

            <div className="form-group mt-2">
              <label>
                {modalType === "category" ? "Category Name" : "Service Name"}
              </label>
              <input
                type="text"
                value={modalData.name}
                onChange={(e) =>
                  setModalData({ ...modalData, name: e.target.value })
                }
                className="form-control"
                placeholder="Enter name"
              />
            </div>

            <div className="modal-actions mt-3">
              <button
                className="btn btn-secondary me-2"
                onClick={() => setModalOpen(false)}
              >
                Cancel
              </button>
              <button className="btn btn-success" onClick={handleModalSubmit}>
                Save
              </button>
            </div>

            {/* {modalMode !== "edit" && (
              <p
                className="mt-3 text-primary"
                style={{ cursor: "pointer" }}
                onClick={() =>
                  setModalType(
                    modalType === "category" ? "service" : "category"
                  )
                }
              >
                {modalType === "category"
                  ? "Want to add a Service instead?"
                  : "Want to add a Category instead?"}
              </p>
            )} */}
          </div>
        </div>
      )}

      {/* Delete Confirmation Modal */}
      {deleteConfirm.open && (
        <div className="modal-overlay">
          <div className="modal-content text-center">
            <h4>Confirm Deletion</h4>
            <p>Are you sure you want to delete this category?</p>
            <div className="modal-actions">
              <button
                className="btn btn-secondary me-2"
                onClick={() => setDeleteConfirm({ open: false, id: null })}
              >
                Cancel
              </button>
              <button className="btn btn-danger" onClick={handleDelete}>
                Delete
              </button>
            </div>
          </div>
        </div>
      )}

      {show && (
        <>
          {/* Modal */}
          <div className="modal fade show d-block" tabIndex="-1" style={{padding:"20px"}}>
            <div className="modal-dialog modal-dialog-centered">
              <div className="modal-content">
                <div className="modal-header">
                  <h5 className="modal-title">Bulk Upload Categories</h5>
                  <button
                    type="button"
                    className="btn-close"
                    onClick={() => {
                      setShow(false);
                      setMessage("");
                    }}
                  ></button>
                </div>
                <div className="modal-body">
                  <div className="alert alert-info">
                    <strong>üìå Instructions:</strong>
                    <ul className="mb-0">
                      <li>
                        Download the sample Excel file and fill in your data.
                      </li>
                      <li>
                        Do not change the column headers in the sample file.
                      </li>
                      <li>Before adding category create categories</li>
                      <li>
                        Make sure all required fields are filled before
                        uploading.
                      </li>
                      <li>
                        Only <code>.xls</code> and <code>.xlsx</code> formats
                        are supported.
                      </li>
                    </ul>
                  </div>

                  <div className="mb-3">
                    <label className="form-label">Choose Excel File</label>
                    <input
                      type="file"
                      accept=".xlsx,.xls"
                      className="form-control"
                      onChange={handleFileChange}
                    />
                  </div>

                  {message && (
                    <div className="mt-3">
                      <div
                        className={`alert ${
                          message?.text?.startsWith("‚úÖ")
                            ? "alert-success"
                            : "alert-danger"
                        }`}
                      >
                        {message?.text}
                      </div>

                      {message.createdCount !== undefined && (
                        <p>
                          ‚úÖ <strong>{message.createdCount}</strong> services
                          created <br />
                          ‚ö†Ô∏è <strong>{message.skippedCount}</strong> rows
                          skipped
                        </p>
                      )}

                      {message.skippedRows &&
                        message.skippedRows.length > 0 && (
                          <div className="alert alert-warning mt-2">
                            <strong>Skipped Rows:</strong>
                            <ul className="mb-0">
                              {message.skippedRows.map((row, idx) => (
                                <li key={idx}>
                                  Row {row.row}: {row.reason}
                                </li>
                              ))}
                            </ul>
                          </div>
                        )}
                    </div>
                  )}

                  <div className="d-flex justify-content-between">
                    <a
                      href="/sample/services_bulk_upload.xlsx"
                      download
                      className="btn btn-outline-primary"
                    >
                      üì• Download Sample File
                    </a>
                    <button
                      className="btn btn-success"
                      onClick={handleUpload}
                      disabled={loading}
                    >
                      {loading ? "Uploading..." : "Upload"}
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>

          {/* Backdrop as a sibling */}
          <div className="modal-backdrop fade show"></div>
        </>
      )}

      <style>
  {`
  /* Responsive layout for small screens (‚â§549px) */
  @media (max-width: 550px) {
    .category-header {
      flex-direction: column;
      align-items: flex-start;
      gap: 8px;
    }

    .bulk-addmore {
      width: 100%;
      justify-content: flex-start;
      flex-wrap: wrap;
      gap: 8px;
    }
  }
  `}
</style>
      <ToastContainer position="top-right" autoClose={3000} />


    </div>
  );
};

export default RegisteredServicesLayer;
